import { useDispatch, useSelector } from '../../services/hooks';
import styles from './style.module.css';
import { useState, useRef } from 'react';
import { Input, Button} from '@ya.praktikum/react-developer-burger-ui-components';
import { registerUserThunk } from '../../services/actions/user';
import { Redirect, Link } from 'react-router-dom';
import FormWrapper from '../../components/form-wrapper/form-wrapper';

function RegisterPage() {
    const dispatch = useDispatch();
    const [form, setForm] = useState({
        name: '',
        email: '',
        password: '',
    });
    const [passwordIcon, setPasswordIcon] = useState<any>('ShowIcon');
    const passwordRef = useRef<HTMLInputElement>(null);
    
    const { registerRequest, isAuthorised, registerErrorMessage } = useSelector(state => state.user)

    if (isAuthorised) return (<Redirect to={{ pathname: '/' }} />);

    const onSubmit = (e) => {
        e.preventDefault();
        const formData = { ...form };
        if (formData.email && formData.password && formData.name) {
            dispatch(registerUserThunk(formData));
        }
    }

    const onChange = e => setForm({ ...form, [e.target.name]: e.target.value });

    const onIconClick = () => {
        if (passwordRef.current) {
            if (passwordRef.current.type === 'password') {
                passwordRef.current.type = 'text';
                setPasswordIcon('HideIcon');
            } else {
                passwordRef.current.type = 'password';
                setPasswordIcon('ShowIcon');
            }
        }
    }

    if (registerRequest) {
        return (
            <FormWrapper>
                <p className="text text_type_main-medium">...</p>
            </FormWrapper>
        );
    }
    
    return (
        <FormWrapper>
            <h2 className="text text_type_main-medium"></h2>
            <form onSubmit={onSubmit} className={styles.form}>
                <div className="mt-6"><Input
                    type={'text'}
                    placeholder={''}
                    value={form.name}
                    name={'name'}
                    onChange={onChange}
                    />
                </div>
                <div className="mt-6">
                    <Input
                        type={'email'}
                        placeholder={'E-mail'}
                        value={form.email}
                        name={'email'}
                        onChange={onChange}
                    />
                </div>
                <div className="mt-6">
                    <Input
                        type={'password'}
                        icon={passwordIcon}
                        onIconClick={onIconClick}
                        placeholder={''}
                        value={form.password}
                        ref={passwordRef}
                        name={'password'}
                        onChange={onChange}
                    />
                </div>
                { registerErrorMessage && (
                    <div className={`mt-6 ${styles.error}`}>
                        <p className="text text_type_main-default">{ registerErrorMessage }</p>
                    </div> )
                }
                <div className="mt-6">
                    <Button type="primary" size="medium"></Button>
                </div>
            </form>
            <div>
                <p className="text text_type_main-default"> ?&nbsp;
                    <Link to="/login" className={styles.link}></Link>
                </p>
            </div>
        </FormWrapper>
    )
}

export default RegisterPage;